img{
    max-width: 100%;
    height: auto;
}
a{
    text-decoration:none;
    display: block;
}
*,*::before,*::after{
    box-sizing: border-box;
}
ul,li,.row,h1,h2,h3,h4,h5,h6{
    margin: 0px;
    padding: 0px;
}
.text-hidden{
    text-indent: 101%;
	white-space: nowrap;
	overflow: hidden;
}